.grid-item {
	padding-bottom: 10px;
	position: relative;
	margin-right: 16px;
	max-width: 180px;
	width: 34vw;
}

.grid-spacer img {
	opacity: 0;
	width: 9vw;
}

.grid-item > .jumper {
	position: absolute;
	width: 100%;
	height: 100%;
	transition: background 0.2s linear;
}
.grid-item > .jumper:hover {
	background: rgba(0, 0, 0, 0.2);
}

.float-right {
	float: right;
}

.control span {
	font-size: 30px;
	margin-right: 30px;
	cursor: pointer;
	outline: none;
}

.control span:hover {
	opacity: 0.5;
}

.grid-item .ui.placeholder {
	max-height: 240px;
	height: 44vw;
	width: 34vw;
	max-width: 180px;
}

.grid-item img {
	max-height: 240px;
	height: 44vw;
	width: 34vw;
	max-width: 180px;
	object-fit: cover;
	object-position: top;
	border-radius: 4px;
}

.grid-item .time {
	position: absolute;
	background-color: rgba(0, 0, 0, 0.5);
	color: white;
	padding: 3px 5px;
	font-size: 0.8em;
	right: 0;
}

.f-bold {
	font-weight: bold;
}

.ellipsis {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.lds-ring {
	display: inline-block;
	position: relative;
	width: 40px;
	height: 40px;
}
.lds-ring div {
	box-sizing: border-box;
	display: block;
	position: absolute;
	width: 32px;
	height: 32px;
	margin: 4px;
	border: 4px solid #4183c4;
	border-radius: 50%;
	animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
	border-color: #4183c4 transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
	animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
	animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
	animation-delay: -0.15s;
}
@keyframes lds-ring {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

@keyframes animate-fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.scale-05 {
	transform: scale(0.5);
}

.text-center {
	text-align: center;
}

.text-right {
	text-align: right;
}

.video-container {
	width: 100%;
	height: 100%;
	/* margin-bottom: 30px; */
}

.video-container i.icon {
	margin: 0 0.25em !important;
}

.series-img {
	width: 100%;
	height: 240px;
	object-fit: cover;
	object-position: top;
}

.grid-wrap {
	display: flex;
	flex-wrap: wrap;
	max-width: 1400px;
	margin-left: auto;
	margin-right: auto;
	/* justify-content: center; */
}

.wrap-item {
	flex-basis: 25%;
	position: relative;
	transition: all 0.2s linear;
	background: white;
	height: 45vw;
	max-height: 300px;
	/* z-index: 1; */
}

.h-100 {
	height: 100% !important;
	max-width: 100% !important;
}

.wrap-item:hover .wrap-info {
	opacity: 0.5;
}

.wrap-item img {
	width: 100%;
	height: 100%;
	/* max-height: 300px; */
	object-fit: cover;
	object-position: top;
}

.wrap-item .wrap-info {
	transition: opacity 0.3s linear;
	opacity: 1;
	position: absolute;
	height: 100%;
	top: 0;
	left: 0;
	padding: 10px;
	background: rgba(0, 0, 0, 0.2);
	width: 100%;
	text-align: right;
	z-index: 1;
	color: white;
}

.img-back {
	position: fixed;
	top: 0;
	left: 0;
	z-index: -1;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: top;
	filter: blur(5px);
	opacity: 0.12;
}

.expanded-y {
	display: flex;
	justify-content: space-between;
}

.fixed-form {
	display: none;
}

.fixed-bottom {
	position: fixed;
	bottom: 0;
	right: 15px;
}

.rounded {
	padding: 5px !important;
	width: 48px !important;
	/* max-width: 999px; */
	height: 48px !important;
	border-radius: 50% !important;
}

.centered {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 80vh;
	max-width: 500px;
	padding: 15px;
	margin: 0 auto;
}

.hide-lg {
	display: none;
}

.opacity-0 {
	opacity: 0;
}

.opacity-1 {
	opacity: 1;
}

@media only screen and (max-width: 600px) {
	.grid-item {
		margin-right: 8px;
		/* width: 24vw; */
	}

	.mb-sm-70 {
		margin-bottom: 70px;
	}

	.pt-sm-20 {
		padding-top: 20px;
	}

	.hide-lg {
		display: block;
	}

	.fixed-form {
		display: flex;
		flex-direction: column;
		align-items: center;
		position: fixed;
		justify-content: flex-end;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(255, 255, 255, 0.4);
		transition: opacity 0.2s linear;
	}

	.fixed-form form {
		width: 100%;
		padding: 1em;
	}

	.grid-scroll-blocker {
		display: none;
	}

	#auth .ui.container {
		width: inherit !important;
		max-width: 100%;
	}

	.fixed-video {
		position: fixed;
		width: 100vw;
		display: flex;
		flex-direction: column;
		align-items: stretch;
		max-height: 100%;
	}

	.comment-section {
		flex-grow: 1;
		margin: 0 1em 40px 1em;
		overflow-y: auto;
		padding-bottom: 40px;
		scrollbar-width: none;
	}

	.comment-section::-webkit-scrollbar {
		display: none;
	}

	.video-spacer {
		height: 70vw;
		max-height: 340px;
	}

	.full {
		margin: 0 -1em;
	}

	.m-1 {
		margin: 1em;
	}

	.grid-spacer img {
		width: 1em;
	}

	.wrap-item {
		flex-basis: 33.3%;
	}

	.wrap-item .wrap-info {
		opacity: 1;
	}

	.hide-xs {
		display: none;
	}

	.grid-item p {
		font-size: 0.8em;
	}

	/* .grid-item img {
		height: 31vw;
		width: 24vw;
		object-fit: cover;
	} */

	.wrap-item p {
		font-size: 0.7em;
	}

	.grid-list > .grid-container {
		padding-left: 20px;
	}

	.grid-list .title {
		padding-left: 20px;
	}

	.grid-container::-webkit-scrollbar {
		display: none;
	}
}

