}

.tab-container {
	position: fixed;
	bottom: 0;
	display: flex;
	justify-content: center;
	width: 100%;
	background: white;
	z-index: 3;
}

.tab-item {
	text-align: center;
	margin: 0 1.2em;
	padding: 0.4em 0;
	cursor: pointer;
	border-top: 1px solid #f9f9f9;
}

.tab-label {
	font-size: 10px;
}

.tab-active .tab-icon {
	font-weight: bold;
}
